.love-label {
	color: hsl(301, 76%, 61%);
  font-size: 30px;
  
	font-family: 'Luckiest Guy', cursive;
	height: 50px;
	left: 0;
	margin-top: 15px;
	letter-spacing: 5px;
	text-align: center;
	text-transform: uppercase;
	top: 50%;
	width: 100%;
  animation: move linear 1000ms infinite;    
  }
  
  @keyframes move {
	0% {
	  text-shadow:
		4px -4px 0 hsl(125, 100%, 50%, 1) , 
		3px -3px 0 hsl(125, 100%, 50%, 1), 
		2px -2px 0 hsl(125, 100%, 50%, 1), 	
		1px -1px 0 hsl(125, 100%, 50%, 1),
		-4px 4px 0 hsla(180, 100%, 50%, 1), 
		-3px 3px 0 hsla(180, 100%, 50%, 1), 
		-2px 2px 0 hsla(180, 100%, 50%, 1), 
		-1px 1px 0 hsla(180, 100%, 50%, 1)
	  ;
	}
	25% {    
	  text-shadow:      
		-4px -4px 0 hsla(180, 100%, 50%, 1), 
		-3px -3px 0 hsla(180, 100%, 50%, 1), 
		-2px -2px 0 hsla(180, 100%, 50%, 1), 
		-1px -1px 0 hsla(180, 100%, 50%, 1),
		4px 4px 0 hsl(125, 100%, 50%, 1), 
		3px 3px 0 hsl(125, 100%, 50%, 1), 
		2px 2px 0 hsl(125, 100%, 50%, 1), 
		1px 1px 0 hsl(125, 100%, 50%, 1)      
	  ;
	}
	50% {
	  text-shadow:
		-4px 4px 0 hsl(125, 100%, 50%, 1), 
		-3px 3px 0 hsl(125, 100%, 50%, 1), 
		-2px 2px 0 hsl(125, 100%, 50%, 1), 
		-1px 1px 0 hsl(125, 100%, 50%, 1),
		4px -4px 0 hsla(180, 100%, 50%, 1), 
		3px -3px 0 hsla(180, 100%, 50%, 1), 
		2px -2px 0 hsla(180, 100%, 50%, 1), 
		1px -1px 0 hsla(180, 100%, 50%, 1)
	  ;
	}
	75% {
	  text-shadow:
		4px 4px 0 hsla(180, 100%, 50%, 1), 
		3px 3px 0 hsla(180, 100%, 50%, 1), 
		2px 2px 0 hsla(180, 100%, 50%, 1), 
		1px 1px 0 hsla(180, 100%, 50%, 1),
		-4px -4px 0 hsl(125, 100%, 50%, 1), 
		-3px -3px 0 hsl(125, 100%, 50%, 1), 
		-2px -2px 0 hsl(125, 100%, 50%, 1), 
		-1px -1px 0 hsl(125, 100%, 50%, 1)      
	  ;
	}
	100% {
	  text-shadow:
		4px -4px 0 hsl(125, 100%, 50%, 1), 
		3px -3px 0 hsl(125, 100%, 50%, 1), 
		2px -2px 0 hsl(125, 100%, 50%, 1), 
		1px -1px 0 hsl(125, 100%, 50%, 1),
		-4px 4px 0 hsla(180, 100%, 50%, 1), 
		-3px 3px 0 hsla(180, 100%, 50%, 1), 
		-2px 2px 0 hsla(180, 100%, 50%, 1), 
		-1px 1px 0 hsla(180, 100%, 50%, 1)
	  ;
	}  
  }


